import React, { Component } from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'

import PageWrapper from './PageWrapper'

const devData = [
  { customer: 'Helsinki University', startDate: '11.11.2017', endDate: '03.23.2018', address: 'Merikatu 14 A, Heslinki', contact: 'Pekka Hmmlainen',  contactPhone: '0432348746', contactEmail: 'pekkH@hsy.fi' },
  { customer: 'Lapalainen, Juuko', startDate: '28.10.2017', endDate: '01.09.2018', address: 'Riistapolku 14 A, Espoo', contact: 'Juuko Lapalainen',  contactPhone: '093334546', contactEmail: 'juuLap@gmail.com', active: 'true' },
  { customer: 'City of Espoo', startDate: '04.08.2017', endDate: '24.05.2019', address: 'Rantapolku 23 C, Helsinki', contact: 'Janne Kaaja',  contactPhone: '084384565', contactEmail: 'kjann@yahoo.com' },
  { customer: 'Kiviniemi, Tatu', startDate: '02.08.2017', endDate: '12.11.2017', address: 'Mannerhieminkatu 1, Helsinki', contact: 'Tatu Kiviniemi',  contactPhone: '054384368', contactEmail: 'tatu@aol.com', active: 'true' }
]
const NewContractButton = styled(Link)`
  position: absolute;
  bottom: 25px;
  right: 25px;
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  background: ${p => p.theme.primaryColor};
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 100px;
  border: none;
  box-shadow: 0 0 2px 2px #50453f4f;
  text-decoration: none;
`
const Panel = styled.li`
  flex: 1 7;
  transition: all 0.4s ease;
  transition-delay: 0s;
  background: ${({ active }) => active ? 'radial-gradient(circle at top left, #3b3b56, #3b3b56, #2a2a3e)' : 'radial-gradient(circle at top left, #c1c1c1, #c1c1c1, #6f5a3e87)'};
  color: ${({ active }) => active ? '#f1c965' : 'black'};
  display: flex;
  flex-direction: column;
  width: 95%;
  margin: auto;
  padding: 10px 8px;
  box-shadow: 0px 0px 11px 1px #676767a3;
  justify-content: space-around;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 0.35rem;
  h2 {
    margin: 0;
  }
  p {
    margin: 0.3rem;
  }
`
const PanelHeader = styled.div`
  display: flex;
  h2{
    flex:1 1;
  }
  a {
    color: inherit;
  }
`
const Icon = styled.i`
  margin: 0.3rem;
  padding: 3px;
  font-size: 1.5rem;
  border-radius: 5px;
  border: 2px solid;
  cursor: pointer;
  text-decoration: none;
`
const ContractsWrapper = styled.ul`
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 0;
  margin: 0;
  box-shadow: 4vh 7vh 14vh -6vh rgba(0, 0, 0, 0.35), -4vh 7vh 14vh -6vh rgba(0, 0, 0, 0.35);
`

class ContractsPage extends Component {
  loadContracts = () => {
    console.log('loading contracts...')
  }

  render() {
    return (
      <PageWrapper title='Contracts'>
        <ContractsWrapper>
          {devData.map((contract, i) =>
            <Panel active={contract.active} key={i}>
              <PanelHeader>
                <h2>{contract.customer}</h2>
                <a href={`mailto:${contract.contactEmail}`}><Icon className="fa fa-fw fa-envelope" /></a>
                <a href={`tel:${contract.contactPhone}`}><Icon className="fa fa-fw fa-phone" /></a>
              </PanelHeader>
              <p>{contract.address}</p>
              <p>{contract.contact}</p>
            </Panel>
          )}
        </ContractsWrapper>

        <NewContractButton to='/new'>+</NewContractButton>

      </PageWrapper>
    )
  }
}

export default ContractsPage



